<template>
<div id="player-mv">
    <div id="playing">
        <!-- 
            H5 webkit-playsinline playsinline
            H5H5 x5-video-player-type="h5"
        -->
         <video  webkit-playsinline playsinline x5-video-player-type="h5" controls :src="mvurl" :poster="mv.cover" ></video>
    </div>
    <div id="title">
        <img src="../../assets/imgs/recommend.png" alt="">
        <!-- props -->
        <span class="mvtitle">{{mv.name}}</span>
    </div>
    <div id="infoOfMv">
        <span>{{mvCount(mv.playCount)}}</span>
        <span>{{mv.publishTime}}</span>
    </div>
    <Yuermvbtns/>
    <hr class="splitline">
    <div id="author">
        <img :src="mvartist.picUrl" alt="">
        <span class="author-name">{{mv.artistName}}</span>   
        <div class="author-right">
            <img src="../../assets/yuer-musiclist/add-list.png" alt="">
            <p></p>
        </div>
    </div>
    <hr class="boline">
</div>
</template>


<script>

import Yuermvbtns from './yuer-mvbtns'

export default {
    components: {
        Yuermvbtns
    },
    computed: {
        mv(){
            return this.$store.state.mv
        },  
        mvurl(){
            return this.$store.state.mvurl
        }, 
        mvartist(){
            return this.$store.state.mvartist
        } 
      
    },
    //$route
    watch: {
      '$route':{
        deep:true,
        handler(){
            this.$router.go()
        }
      }  
    },
    mounted () {
     
        this.getAllOfMv()
    },
    methods: {
        mvCount(Count){
            return Count > 10000 ?  String(Count).slice(0,-4)+'' : Count
        },
        async getAllOfMv(){
            let mvid = this.$route.params.mvid
            //mv 1
            let type = 1
            this.$store.dispatch('getHotComments',{mvid,type})
            await this.$store.dispatch('getPlayingMV',mvid)

            //
            let artistName = this.$store.state.mv.artistName
            this.$store.dispatch('getmvartist',artistName)
        }
    }
    
}
</script>

<style>

@import '../../css/yuer-playmv.css';

</style>


