<template>
<div class="search-header">
    <div  class="header-top "> 
        <img class="backto" src="../assets/yuer-musiclist/back.png" @click="$router.go(-1)">
        <input placeholder="" type="text" class="search-input" v-model="$store.state.keyword">
        <img class="searchinlist" src="../assets/yuer-musiclist/searchinlist.png" alt="" @click="search">
    </div>
</div>
</template>

<script>
/*
 
    1.
    allmusicsearchresult
    2.
    yuer-searchallmuiscsearchresult
    3.
    searchresultallmusic
    
    
    searchresult

    
    


    allmusichistory
*/

import Yuerlistmain from '../components/yuer-musicList/yuer-listmain'
   export default {
       components: {
           Yuerlistmain
       },
       data(){
            return{
               
            }
        },
        mounted () {
            this.$store.state.issearch = true
        },
        methods: {
            search(){
                if(!this.$store.state.isMainSearch){
                    this.searchInList()
                }else{
                    this.searchInMain()
                    this.$router.replace('/main/search/music')
                }
            },
            //
            searchInList(){
                
                //RegExp

                //searchresult
                if(this.keyword !== null){
                    let conditon = new RegExp(this.keyword,'i')
                    this.$store.state.allmusic.forEach( music => {
                        if(conditon.test(music.name) ||conditon.test(music.ar[0].name) ){
                            this.$store.state.searchresult.push(music)
                        }
                    })
                    // this.$store.state.allmusic = this.$store.state.searchresult
                }
            },
            searchInMain(){

                //allmusic
                this.$store.dispatch('getResultByKey',{offset:0,type:1})
                this.$store.dispatch('getResultByKey',{offset:0,type:1004})
                this.$store.dispatch('getResultByKey',{offset:0,type:1000})
                
            }
        },
        //
        destroyed(){
            this.$store.state.issearch = false
        } 
   }
</script>

<style>

@import '../css/yuer-search.css';
.search-header{
    position: fixed;
    width: 100%;
    height: 8.2%;
    z-index: 10;
    background-color: black;
}
.search-input{
    background-color: rgb(17, 17, 17);
    width: 70%;
    height: 90%;
    font-size: 20px;
    letter-spacing: 2px;
    color: white;
    padding: 0 15px;
    border: none;
    border-bottom: 1px solid white;
}

</style>




