<template>
<div id="yuer-middle">
    <div id="middle-rotchare" ref="middleRotchare" style="left:-100%;">     
        <img v-for="(img,i) in pics"  :key="i"  :src="img !== undefined ? img.imageUrl :'' " alt="" class="rotchartPic" @touchstart="touchChart($event,i+1)" @touchmove="touchmove" @touchend="touchend">
   </div>
    <div id="rotchare-dots">
        <span v-for="(dot,i) in dots" :key="i" :class=" index === dot ? 'focus':''"></span>
    </div> 
</div>
</template>

<script>
   export default {
       data () {
           return {
               //
               index:1,              
               //touchmove
               ortouchX:0,
               //touchmove0
               entouchX:0,
               //
               orileft:0,
               //id
               chartid:"",
               //
               Clength:9,
               //
               dots:[1,2,3,4,5,6,7,8,9]
           }
       },
        
       mounted () {
            //
            this.autoChart()
            
       },
       destroyed () {
           //
            clearInterval(this.chartid)
       },
       computed: {
            pics(){
                let banners = this.$store.state.banners
                banners.push(banners[0])
                banners.unshift(banners[this.Clength-1])
                console.log(banners);
                return banners
            }
       },
       methods: {
           //
            autoChart(){
                this.chartid = setInterval(this.callback, 3000)
            },

            //
            callback(){
                this.$refs.middleRotchare.style.transition = '1s'
                    if( this.index < this.Clength + 1){
                        this.index ++
                    }else{
                        this.index = 1
                    }
                    
                //
                this.toNextPic()

                //1()
                if(this.index == 1){
                    clearInterval(this.chartid)
                    this.chartid = setInterval(this.callback,0)
                }else{
                    clearInterval(this.chartid)
                    this.chartid = setInterval(this.callback,3000)
                }
                        
            },
            
            //
            toNextPic(){
                this.$refs.middleRotchare.style.left = `-${100*(this.index)}% ` 
                //1s
                if( this.index == this.Clength + 1 ){
                    setTimeout(()=>{
                        this.jumpToPic(1)
                    },1000) 
                }else if(this.index == 0){
                    setTimeout(()=>{
                        this.jumpToPic(this.Clength)
                    },1000) 
                }
            },

            //
            jumpToPic(n){
                this.$refs.middleRotchare.style.transition = '0s'
                this.$refs.middleRotchare.style.left = `-${100*n}% ` 
                this.index = n 
            },
        
            //
            touchChart(e,id){
            //
                //this.istouchendfalse touchendthis.istouchendfalse
                // touchend() this.istouchendtruetrue
                
                /*
                    div1s
                    idindex+1,
                */
                if(id == this.Clength + 2){
                    this.jumpToPic(1)
                }
                else if(id == 1){
                    this.jumpToPic(this.Clength)
                }else{
                     this.$refs.middleRotchare.style.transition = '1s'
                }


                //
                this.entouchX = this.ortouchX = e.touches[0].clientX
                //left
                this.orileft = this.$refs.middleRotchare.offsetLeft
                //
                this.$refs.middleRotchare.style.transition = '0s'

                //
                clearInterval(this.chartid)
                
            },

            //
            touchmove(e){
                let tempX = this.orileft + this.entouchX - this.ortouchX
                this.entouchX = e.touches[0].clientX
                this.$refs.middleRotchare.style.left = tempX + 'px'
            },

            //
            shortTouch(e){

            },
            //
            dragTouch(){  
                //
                let oriindex = this.index
                // this.$refs.middleRotchare.style.transition = '1s'
                //
                if(this.entouchX > this.ortouchX ){
                    if( this.index <= 0 ){
                        this.index = this.Clength
                    }else{
                        this.index--
                    }
                }
                //
                else{
                    if( this.index < this.Clength + 1 ){
                       this.index ++
                    }else{
                        this.index = 1
                    }
                    
                }
                this.toNextPic()
                
            },
            //
            touchend(e){
                /*
                    touchmouvetouchstart

                    touchstart
                    move
                    movethis.entouchX
                    
                    touchendistouchend,istouchendtrue
                    touchtouchstartistouchendfalsetouchendistouchendtrue
                    touchstartistouchendfalsetouchendtouchstartistouchendtrue
                */
                if(this.entouchX !== this.ortouchX ){
                    this.dragTouch()
                    this.$refs.middleRotchare.style.transition = '1s'              
                }else{
                    console.log("");
                }

                //
                this.autoChart()
            },
            //      
       }
   }
</script>

<style>
@import '../../css/yuer-middle-rotchart.css';
.focus{
    background: red !important;
}
</style>


